.header__nav {
  display: none;
}

.logobar {
  display: flex;
  justify-content: space-between;
  background-color: pink;
  
  padding: 20px 25px;
  
  &__logo {
    height: 24px;
  }
  
  &__menu-opener {
    height: 8px;
  }

  @media (min-width: 769px) {
    padding: 28px 18px;
  }

  @media  (min-width: 1200px) {
    padding: 32px;
      
    .logobar__menu-opener {
      display: none;
    }
    .header__nav {
      display: flex;
      flex-direction: row;
    }

    .nav-contact {
      display: flex;
      flex-direction: row;
      &__link {
        text-transform: uppercase;
        margin-left: 7px;
        color: $accent-orange;

      }
    }
    .nav__item {
      margin-left: 64px;
    }
  }
}

.banner {
  padding: 32px 16px;
  background-color: pink;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  text-align: center;

  &__paragraph {
    margin-bottom: 32px;
  }

  &__title {
    color: $accent-orange;
    font-size: 64px;
    font-weight: 400;
    letter-spacing: -0.05em;
    line-height: 54.4px;
  }

  &__picture {
    height: 290px;
    // width: 288px;
    background-image: url(/images/cookie.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  
    align-items: center;
    justify-content: center;
  }
}

