.button {
  width: 288px;
  border: 2px solid $accent-orange;
  border-radius: 50px;
  background-color: transparent;
  padding-top: 20px;
  padding-bottom: 20px;

  &__name {
    margin-left: 6px;
  }

  &__content {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
}
